	import service from '../../service.js';
    import {
        mapState,
        mapMutations
    } from 'vuex'
	
    export default {
        computed: {
            ...mapState(['hasLogin', 'forcedLogin'])
        },
        methods: {
            ...mapMutations(['logout','islogin']),
			isLogin(){
				this.islogin();
				console.log(this.hasLogin);
				console.log();
			},
            bindLogin() {
                uni.navigateTo({
                    url: '../login/login',
                });
            },
            bindLogout() {
                this.logout();
                /**
                 * 如果需要强制登录跳转回登录页面
                 */
                if (this.forcedLogin) {
                    uni.reLaunch({
                        url: '../login/login',
                    });
                }
            },
			realse(){
				uni.navigateTo({
					url: '../publish/publish',
				});
			}
        },
		onLoad (){
			this.isLogin();
			
			let userInfo=service.getUsers();
			console.log(userInfo);
			if(userInfo.length>0){
				console.log(111);
				console.log(userInfo[0].account);
			}else{
				console.log(this.hasLogin);
			}
		}
    }
